import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false
Vue.prototype.n = ""
Vue.prototype.setPic = function(code,src){
  var that = this;
  uni.request({
  	url: 'http://106.13.239.202/index.php?c=uploadimg&m=setImg',
  	method: 'POST',
  	header: {"content-type": "application/x-www-form-urlencoded"},
  	data: {
		code:code,
		src:src
  		
  	},
  	success: res => {
  		
  	       
  	}
  });
}

Vue.prototype.getAddress = function(){
  var that = this;
  var address;
  // 读取本地手机号
  var phone = this.getPhone();
  // 向数据库查询门牌号
  uni.request({
  	url: 'http://106.13.239.202/index.php?c=approve&m=find',
  	method: 'POST',
  	header: {"content-type": "application/x-www-form-urlencoded"},
  	data: {
		phone:phone
  	},
  	success: res => {
  		address = res.data.data.address;
  	}
  });
  return address;
}
// 查询数据库
Vue.prototype.getName = function(){
  var that = this;
  // 读取本地手机号
  var phone = this.getPhone();
	var p;
  // 向数据库查询门牌号
  uni.request({
  	url: 'http://106.13.239.202/index.php?c=approve&m=find',
  	method: 'POST',
  	header: {"content-type": "application/x-www-form-urlencoded"},
  	data: {
		phone:phone
  	},
  	success: res => {
  		 p = res.data.data.name;
		 p = "dad";
  	}
  });
  
  return  p;
}
Vue.prototype.getPhone = function(){
	var out;
	uni.getStorage({
      key: 'phone',
      success: function (res) {
		   out = res.data;
			
  		
      }
	});
  return out;
}

Vue.prototype.isApprove = function(){
  
  	if(this.getName()){
  		return true;
  	}else{
  		return false;
  	}
  
}

			
App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()